<template>
    <div class="outdoor-photography-page">
      <!--<div class="analysis-object-tree-warp">-->
        <!--&lt;!&ndash;<water-radio-tree></water-radio-tree>&ndash;&gt;-->
      <!--</div>-->
      <div class="secondary-page-mode">
        <div class="photography-big-picture">
          <button type="button"
                  @click = "prevTogglePicture"
                  class="prev-btn">
            <i class="el-icon-arrow-left"></i>
          </button>
          <div class="big-picture">
            <img :src="bigPicture" :alt="bigAlt">
          </div>
          <button type="button"
                  @click = "nextTogglePicture"
                  class="next-btn">
            <i class="el-icon-arrow-right"></i>
          </button>
          <div class="picture-title">
            <span class="video-name">当前站点：{{ stationName }}</span>
          </div>
        </div>
        <div class="photography-small-picture">
          <h4 class="currency-mode-title">
            <i class="mode-title-icon"></i> 图片列表
            <span class="mode-title-line"></span>
          </h4>
          <el-scrollbar id="photography-small-scroll" class="photography-small-scroll">
            <ul class="small-img-list">
              <li :class="{active: smallActive === index}"
                  :key = "index"
                  @click = "togglePhotography(item,index)"
                  v-for="item,index in smallList">
                <div class="small-img-box">
                  <img :src="item.src" :alt="item.name">
                </div>
                <p class="small-img-title">{{ item.name }}</p>
              </li>
            </ul>
          </el-scrollbar>
        </div>
      </div>
    </div>
</template>

<script>
  // import waterRadioTree from '@/components/currency/waterRadioTree'
  // import {formatDate} from '../../dateformat.js'
  let smallList = [];
  export default {
      name: "outdoorPhotography",
      data(){
          return{
            smallList,
            smallActive:0,
            bigPicture:'http://img.ys7.com/group3/M00/7C/BE/CmFnEVug5w2AGc5EAAC77BPpoew308.jpg',
            bigAlt:'2018-07-12 12:00',
            stationId:'',
            stationName:''
          }
      },
      methods:{
        togglePhotography(item,index){//点击小图切换
          this.smallActive = index;
          this.bigPicture = item.src;
          this.bigAlt = item.name;
        },
        captureList(){
          var date=new Date();
          var start=new Date(date.getTime()-86400*1000);
          this.$http.get('/waterenvapi/video/captureList',{
              params:{
                // startTime:formatDate(start,'yyyy-MM-dd hh:mm:ss'),
                // endTime:formatDate(date,'yyyy-MM-dd hh:mm:ss')
                tartTime:start.format('yyyy-MM-dd hh:mm:ss'),
                endTime:date.format('yyyy-MM-dd hh:mm:ss')
              }
            }).then((response)=>{
              let dataJson = response.data;
              if(dataJson.code === 200){
                var list = dataJson.content.dataList;
                var arr=[];
                for(var i=0;i<list.length;i++){                
                  var rd=list[i];
                   if(i==0){
                    this.bigPicture='/waterenvapi'+rd.fileUrl;
                  }
                  arr.push({
                    src:'/waterenvapi'+rd.fileUrl,
                    name:rd.fileName
                  });

                }
                this.smallList=arr;
              }
            }).catch((error)=>{

            });
        },
        prevTogglePicture(){
          let number = this.smallActive-1;
          if(number < 0 ){
            this.smallActive = 0;
            this.$message('这已经是第一张了！');
          }else{
            this.smallActive--;
          }
          this.bigPicture = this.smallList[this.smallActive].src;
          this.bigAlt = this.smallList[this.smallActive].name;
          this.scrollPictureView(this.smallActive*200);
        },
        nextTogglePicture(){
          let number = this.smallActive+1;

          if(number >= this.smallList.length){
            this.smallActive = this.smallList.length-1;
            this.$message('这已经是最后一张了！');
          }else{
            this.smallActive++;
          }
          this.bigPicture = this.smallList[this.smallActive].src;
          this.bigAlt = this.smallList[this.smallActive].name;
          this.scrollPictureView(this.smallActive*200);
        },
        scrollPictureView(top){
          let scroll = document.getElementById('photography-small-scroll');
          let scrollView = scroll.getElementsByClassName('el-scrollbar__wrap')[0];
          let currentlyTop = scrollView.scrollTop;
          let timer = null;
          if(currentlyTop > top){
            timer = setInterval(function(){
              currentlyTop -= 8;
              if(currentlyTop <= top){
                scrollView.scrollTop = top;
                clearInterval(timer);
              }else{
                scrollView.scrollTop = currentlyTop;
              }
            },12);
          }else if(currentlyTop < top){
            timer = setInterval(function(){
              currentlyTop += 8;
              if(currentlyTop >= top){
                scrollView.scrollTop = top;
                clearInterval(timer);
              }else{
                scrollView.scrollTop = currentlyTop;
              }
            },12);
          }
        }
      },
      mounted(){
        this.captureList();
      },
      components:{
        // waterRadioTree
      },
      computed: {
        stationInfo: {
          get: function() {
            return this.$store.state.stationInfo;
          }
        }
      },
      watch: {
        stationInfo(value) {
          if (value.length > 0) {
            this.stationId = value[0].nodeId;
            this.stationName = value[0].nodeName;

          }
        }
      }
  }
</script>

<style scoped lang="less">
.outdoor-photography-page{
  height: 100%;
  .small-img-list{
    padding: 10px;
    li{
      margin-bottom: 10px;
      cursor: pointer;
      .small-img-box{
        height: 160px;
        overflow: hidden;
        img{
          height: 100%;
          width: 100%;
          transition: all 0.2s;
          position: relative;
          z-index: -1;
        }
      }
      .small-img-title{
        line-height: 30px;
        text-align: center;
      }
    }
    li:hover{
      .small-img-box{
        border: 1px solid #b1b1b1;
        img{
          transform: scale(1.4);
        }
      }
    }
    li:last-child{
      margin-bottom: 0;
    }
    li.active{
      .small-img-box{
        box-shadow: 0 0 12px #ffe92f inset;
        border: 1px solid #b1b1b1;
      }
    }
  }
}
.photography-big-picture{
  width: calc(100% - 282px);
  height: 100%;
  float: left;
  background: #616e7e;
  position: relative;
  padding: 40px 70px 76px;
  .prev-btn,.next-btn{
    position: absolute;
    font-size: 40px;
    color: #cccccc;
    font-weight: 700;
    cursor: pointer;
  }
  .prev-btn{
    left: 10px;
    top: 50%;
    margin-top: -26px;
  }
  .next-btn{
    right: 10px;
    top: 50%;
    margin-top: -26px;
  }
  .prev-btn:hover,.next-btn:hover{
    color: #fff;
  }
  .big-picture{
    height: 100%;
    img{
      width: 100%;
      height: 100%;
    }
  }
  .picture-title{
    height: 36px;
    width: 100%;
    line-height: 36px;
    padding: 0 12px;
    color: #fff;
    position: absolute;
    left: 0;
    bottom: 0;
    background: url("../../assets/images/video/video-title-bg.png") repeat-x;
  }
}
.photography-small-picture{
  width: 282px;
  height: 100%;
  float: right;
  border: 1px solid #c6d1de;
}
.photography-small-scroll{
  height: calc(100% - 33px);
}
/* 模块标题通用图标 */
.mode-title-icon{
  display: inline-block;
  vertical-align: middle;
  margin-bottom: 3px;
  width: 16px;
  height: 16px;
  margin-right: 3px;
  background: url("../../assets/images/index/icon_Title_Prefix.png") no-repeat;
}
</style>



